@mixin defaultShadow() {
    box-shadow: 0px 3px 2.91px 0.09px rgba(38, 36, 37, 0.05);
}

@mixin paragrafos($tc: $text, $fw: $fwLight, $fs: 18px, $lh: 1.2, $mb: 25px, $mbLOT: 0, $ta: left) {
    display: block;
    color: $tc;
    font-size: $fs;
    font-weight: $fw;
    margin-bottom: $mb;
    line-height: $lh;
    text-align: $ta;
    &:last-of-type {
        margin-bottom: $mbLOT;
    }
    a {
        color: $secondary;
        font-size: $fs;
        font-weight: $fw;
        line-height: $lh;
        &:hover {
            text-decoration: underline;
            color: $secondaryDark;
        }
        &:visited {
            @include opacity(0.85);
            color: $secondaryDark;
        }
    }
    strong {
        display: inline;
        font-weight: $fwBold;
        color: $tc;
        font-size: $fs;
        line-height: $lh;
    }
    @media screen and (max-width: 430px) {
        font-size: $fs - 2;
        a, strong {
            font-size: $fs - 2;
        }
    }
}

@mixin labels($tc: $text, $fs: 16px) {
    color: $tc;
    font-size: $fs;
    line-height: 1.2;
    font-weight: $fwMedium;
}

@mixin titles($fs: 36px, $tc: $primary, $fw: $fwBold, $ta: left, $ff: $fontFamily__secondary) {
    display: block;
    width: 100%;
    font-size: $fs;
    font-family: $ff;
    font-weight: $fw;
    color: $tc;
    text-align: $ta;
    line-height: 1.2;
}

@mixin card($padding: 25px, $bg: $white, $bc: #EFEFEF) {
    @include roundedAll();
    @include transition();
    display: block;
    position: relative;
    padding: $padding;
    background: $bg;
    border: 1px solid $bc;
    &:hover {
        border: 1px solid darken($bc, 10%);
    }
}