// u
.u-smallObservation {
	display: block;
	width: 100%;
	text-align: center;
	color: $white;
	font-size: 12px;
}

// l-
.l-nav {
	@include transition($ease: ease-in);
	display: block;
	width: 100%;
	max-width: $maxRowWidth;
	margin: 0 auto;
	padding: 20px 0;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	background: $primaryDark;
	z-index: 10;
	.u-containerFlex {
		flex-wrap: wrap;
	}
	
	&__brand {
		@include flexWidth(200px);
		display: block;
		a, img {
			display: block;
			width: 100%;
		}
	}
	&__contentHero {
		@include scroll();
		flex: 1 1 auto;
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: flex-end;
		align-items: center;
		position: relative;
		z-index: 3;
	}
	&__menu {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		position: relative;
		z-index: 2;
		> li {
			display: block;
			position: relative;
			a {
				color: $white;
				cursor: pointer;
				padding: 0 15px;
				&:hover {
					color: $secondary;
				}
			}
		}
	}
	&__social {
		display: block;
		padding-left: 15px;
	}
	&__cta {
		padding-left: 30px;
	}
	
	&__showOnlyInResponsive {
		display: none;
	}
	
	&__maskMenu {
		&.maskAll,
		&.maskMenu {
			@include u-hideElement();
			z-index: 2;
			position: fixed;
			top: 0;
			left: 0;
			right: 0;
			width: 100%;
			height: 100%;
			min-height: 100vh;
			display: block;
			background: rgba(0,0,0,0.15);
			&.is-active {
				@include u-showElement();
			}
		}
		&.maskMenu {
			z-index: 1;
		}
	}
	
	&.hidden {
		top: -300px;
	}
	&.scrolled {
		padding: 5px 0;
		.c-hamburguer {
			&.is-active {
				margin-top: 5px;
			}
		}
	}
}
.l-footer {
	background: $primaryDark;
	padding: 30px 0;
}
.l-headerHome {
	background: $primaryUltraDark;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	position: relative;
	min-height: 600px;
	padding: 80px 0;
	&__cover {
		@include pb(100%);
		@include opacity(0.25);
		display: block;
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		object-fit: cover;
		object-position: center;
	}
	&__slide {
		@include flexWidth(470px);
		display: block;
	}
	&__search {
		flex: 1 1 auto;
		display: block;
		padding-left: 130px;
		padding-top: 40px;
		padding-bottom: 40px;
		position: relative;
		&:before {
			@include opacity(0.2);
			content: '';
			display: block;
			width: 1px;
			height: 100%;
			position: absolute;
			left: 65px;
			top: 0;
			background: $white;
		}
	}
}
.l-homeCentralContent {
	background: $white3;
	display: block;
	padding: 80px 0;
	.u-containerFlex {
		align-items: flex-start;
	}
	&__l {
		flex: 1 1 auto;
		padding-right: 60px;
	}
	&__r {
		display: flex;
		flex-direction: row;
		align-items: flex-start;
		flex-wrap: wrap;
		flex: 1 1 auto;
		min-width: 520px;
		max-width: 570px;
	}
	h1 {
		@include titles();
		margin-bottom: 15px;
	}
}

// s-
.s-associationNumbers {
	display: block;
	padding: 80px 0;
	background: $primary;
	h1 {
		@include titles($tc: $white, $ta: center);
		margin: 0 auto;
		max-width: 715px;
	}
	h2 {
		@include titles($fs: 22px, $tc: $white, $fw: $fwMedium, $ta: center);
		color: $white;
		margin: 25px 0;
	}
	.u-smallObservation {
		margin-top: 15px;
	}
}
.s-testemonials {
	display: block;
	padding: 80px 0;
	background: $mutedGray;
	&__primary {
		display: flex;
		flex-direction: row;
		align-items: flex-start;
		flex-wrap: nowrap;
	}
	.primary__l {
		@include flexWidth(75.6%);
		padding-right: 15px;
	}
	.primary__r {
		flex: 1 1 auto;
		padding-left: 15px;
	}
	
	&__list {
		padding-top: 30px;
		.c-responsiveGrid {
			@include calcWidthForPositionNegative(-15px);
			> li {
				padding: 0 15px;
				display: flex;
				&:last-of-type {
					display: none;
				}
			}
		}
	}
}
.s-recentBlogPosts {
	ul {
		li {
			margin-bottom: 30px;
			padding-bottom: 25px;
			border-bottom: 1px solid $lightGray;
			&:last-of-type {
				margin-bottom: 0;
				padding-bottom: 0;
				border-bottom: 0;
			}
		}
	}
}
.s-recentJobs {
	@include flexWidth(100%);
	> ul > li {
		margin-bottom: 10px;
		&:last-of-type {
			margin-bottom: 0;
		}
	}
}
.s-listsNumbersPerCities {
	@include flexWidth(50%);
	margin-top: 50px;
	&.--left {
		padding-right: 15px;
	}
	&.--right {
		padding-left: 15px;
	}
	&__content {
		display: block;
		margin-bottom: 30px;
	}
	h2 {
		@include titles($fs: 22px, $ff: $fontFamily__default, $tc: $text, $fw: $fwMedium);
		margin-bottom: 5px;
	}
	ul {
		@include card();
		@include defaultShadow;
		li {
			display: block;
			margin-bottom: 10px;
			a {
				display: flex;
				flex-direction: row;
				align-items: center;
				justify-content: space-between;
				&:hover {
					span {
						color: $primary;
						&.caption {
							text-decoration: underline;
						}
					}
				}
			}
			span {
				@include paragrafos($mb: 0);
				display: inline-block;
				&.caption {
					padding-right: 10px;
				}
				&.number {
					font-size: 12px;
					font-weight: $fwBold;
				}
			}
			&.lastReadMore {
				margin-top: 15px;
				padding-top: 15px;
				border-top: 1px solid $mutedGray;
			}
			&:last-of-type {
				margin-bottom: 0;
			}
		}
	}
}

// #page
#page__components {
	padding: 60px 50px;
	.gridComponent {
		display: flex;
		flex-direction: row;
		justify-content: center;
		flex-wrap: wrap;
	}
	.component {
		@include roundedAll(10px);
		flex: 1 1 auto;
		max-width: 380px;
		min-width: 280px;
		width: 100%;
		display: flex;
		flex-direction: column;
		flex-wrap: nowrap;
		margin: 10px;
		background: teal;
		.component__header {
			padding: 15px;
			> h2 {
				color: $white;
				font-size: 18px;
			}
		}
		.component__content {
			@include roundedAll(10px);
			display: block;
			padding: 15px;
			background: $white;
			border: 3px solid teal;
			flex: 1 1 auto;
		}
		.content__hero {
			margin-bottom: 15px;
			padding-bottom: 15px;
			border-bottom: 1px solid $mutedGray;
			&:last-of-type {
				margin-bottom: 0;
				padding-bottom: 0;
				border-bottom: 0;
			}
			> h3 {
				color: $baseGray;
				text-transform: uppercase;
				font-weight: bold;
				font-size: 13px;
				margin-bottom: 4px;
			}
			.obs {
				font-size: 14px;
				color: $text;
				display: block;
				width: 100%;
				margin-top: 5px;
				padding-top: 5px;
				border-top: 1px solid $mutedGray;
				strong {
					font-weight: bold;
				}
				u {
					text-decoration: underline;
				}
				i {
					font-style: italic;
				}
			}
		}
	}
	> section {
		margin-bottom: 40px;
		padding-bottom: 40px;
		border-bottom: 1px solid $mutedGray;
		&:last-of-type {
			margin-bottom: 0;
			padding-bottom: 0;
			border-bottom: 0;
		}
		> h1 {
			color: $primary;
			font-size: 24px;
			text-align: center;
			font-weight: bold;
			margin-bottom: 20px;
		}
	}
	
	.subMenuTeste {
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: space-around;
		padding: 20px;
	}
}

